import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import './header.scss'

const Header = ({ siteTitle }) => (
  <header>
    <div className="menu__wrapper">
      <Link to="/projects/" className="button" href="/projects" activeClassName={'button--active'} >projects</Link>
      <Link to="/about/" className="button" href="/about" activeClassName={'button--active'}>about</Link>
      <Link to="/contact/" className="button" href="/contact" activeClassName={'button--active'}>contact</Link>
      <Link to="/gallery/" className="button" href="/gallery" activeClassName={'button--active'}>gallery</Link>
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
